import React from "react";
import "./User.css";
import userPhoto from "./../../../assets/images/user.png";
import { NavLink } from "react-router-dom";

const User = (props) => {  

  return (
    <div className="user">
      <div className="user__header">
        <NavLink to={"/profile/" + props.userID} className="user__photo">
          <img src={props.photo != null ? props.photo : userPhoto} alt="Alt" />
        </NavLink>
        <button className="user__btn" onClick={props.onToggleFollow} disabled={props.isFollowingProgress.some(u => u === props.userID)}>
          {props.followed ? "UNFOLLOW" : "FOLLOW"}
        </button>
      </div>
      <div className="user__body">
        <div className="user__name">{props.name}</div>
        <div className="user__status">{props.status}</div>
      </div>
    </div>
  );
};

export default User;
