import React from "react";
import "./Post.css";

const Post = (props) => {
  return (
    <div className="post">
      <img
        className="post__avatar"
        src="data:image/jpeg;base64,"
        alt="Alt"
      />
      <div className="post__text">{props.message}</div>
      <div className="post__like">
        Like: <span className="post__like-count">{props.likesCount}</span>
      </div>
    </div>
  );
};

export default Post;