import React from "react";
import User from "./User";

const UserContainer = (props) => {

  let onToggleFollow = () => {
    if (props.followed) {
      props.follow(props.userID);         
    } else {
      props.unfollow(props.userID); 
    }
  };

  return (
    <User {...props} onToggleFollow={onToggleFollow} />
  );
};

export default UserContainer;
