import React from 'react';
import {reduxForm, Field} from "redux-form";
import {Input} from "./../../common/FormsControls/FormsControls";
import {required} from "./../../../utils/validators/validators";

const LoginForm = (props) => {  
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field placeholder={"Login"} component={Input} name={"email"} type="email" validate={[required]} />
      </div>
      <div>
        <Field placeholder={"Password"} component={Input} name={"password"} type="password" validate={[required]} />
      </div>
      <div>
        <Field component={Input} type={"checkbox"} name={"rememberMe"} /> remember me
      </div>
      <div className={props.error && "form-summary-error"}>{props.error}</div>
      <div>
        <button>Login</button>
      </div>
    </form>
  );
};

const LoginReduxForm = reduxForm({
  form: 'login'
})(LoginForm);

export default LoginReduxForm;