import React from "react";
import { NavLink } from "react-router-dom";
import "./Navbar.css";

const Navbar = () => {
  return (
    <nav className="nav">
      <div>
        <NavLink
          to="/profile"
          className="nav__item"
        >
          Profile
        </NavLink>
      </div>
      <div>
        <NavLink
          to="/dialogs"
          className="nav__item"
        >
          Messages
        </NavLink>
      </div>
      <div>
        <NavLink
          to="/news"
          className="nav__item"
        >
          News
        </NavLink>
      </div>
      <div>
        <NavLink
          to="/music"
          className="nav__item"
        >
          Music
        </NavLink>
      </div>
      <div>
        <NavLink
          to="/users"
          className="nav__item"
        >
          Users
        </NavLink>
      </div>
      <div>
        <NavLink
          to="/settings"
          className="nav__item"
        >
          Settings
        </NavLink>
      </div>
    </nav>
  );
};

export default Navbar;
